import React from 'react';
import '../style/main.scss';
import Header from '../components/Header';
import Footer from '../components/Footer';
import Subscription from '../components/Subscription';
import Fetch from '../common/Fetch';
import {response} from '../demo/Demo';
// import Fetch from '../common/Fetch';
import SEO from "../components/seo"


class JobListing extends React.Component{
    state={
        jobList: {},
        currentSection: 'All',
        filterData: [],
        sectionName:[],
        loading: true,
        currentPost:[],
        currentPage: 1,
        postsPerPage: 7,
        pageNumber: []
    }

     componentDidMount() {
         this.setState({ jobList: response.careersList, filterData: response.careersList })
        const section = [...new Set(response.careersList.map(d => d.jobSection))]
        this.setState({ sectionName: section })
        this.createSection();
         this.setPageNumber();
        // this.setPagination()
     }


    createSection=()=> {
        const section = [...new Set(response.careersList.map(d => d.jobSection))]
        this.setState({ sectionName: section })
        }


    // componentDidMount(){
    //     Fetch('career').then(data =>{
    //         if(data.status){
    //             this.setState({jobList: data.response, filterData: data.response, loading: false})
    //             this.createSection();
    //             // this.setPagination()
    //             console.log(data)
    //         }else{
    //             this.setState({isLoading: true})
    //         }
    //     }).catch(e => console.log(e))
    // }
        
    // createSection=()=>{
    //     const section = [...new Set (this.state.jobList.map(d=>d.jobProfile))]
    //     this.setState({sectionName: section})
    //     console.log(this.state.sectionName)
    // }
       
    currentSection=(type='All') => {
        this.setState({ currentSection: type})
        if (type === 'All') {
            this.setState({ filterData: this.state.jobList })
            // this.getCurrentPost();
        } else {
            this.setState({ filterData: this.state.jobList.filter(d => d.jobSection === type) })
            // this.getCurrentPost();
        }
        
    }
    
    getCurrentPost=()=>{
        const {currentPage, postsPerPage, filterData} = this.state;
        const indexOfLastPost = currentPage * postsPerPage;
        const indexOfFirstPost = indexOfLastPost - postsPerPage;
        const currentPost = filterData.slice(indexOfFirstPost, indexOfLastPost)
        this.setState({currentPost})
    }

    setPageNumber=()=>{
        let pageNumber = []
        let totalPosts = this.state.filterData.length
        for (let i = 1; i <= Math.ceil(totalPosts / this.state.postsPerPage); i++) {
            pageNumber.push(i)
            this.setState({pageNumber})
        }
    }
    
    paginate=(pageNumber)=>{
        this.setState({currentPage: pageNumber})
        // let currentUrlParams = new URLSearchParams(window.location.search);
        // currentUrlParams.set('#', pageNumber);
        // this.props.history.push(window.location.pathname + "?" + currentUrlParams.toString());
        let newurl = window.location.protocol + "//" + window.location.host + window.location.pathname + '#' + pageNumber;
        window.history.pushState({ path: newurl }, '', newurl);
    }


    render(){
        const { jobList, 
                currentSection, 
                filterData, 
                sectionName,
                loading,
                currentPost,
                pageNumber
            } = this.state;
            console.log(currentPost, pageNumber)
        return (
            // loading 
            // ?
            // <div> Loading.... </div>

            // :
            <main>
                <SEO title="Jobs" />
                <div className="bg-theme">
                    <div className="header-secondary"><Header /></div>
                    <div className="head-banner">
                        <div className="container">
                            <div className="row">
                                <div className="col-xl-6 col-lg-6 col-md-12 float-none m-auto text-center">
                                    <h1 className="color-white">Careers at OnePrep</h1>
                                    <p className="color-white f-18">Why I say old chap that is spiffing off his nut arse pear shaped
                                plastered Jeffrey bodge barney some dodgy..!!</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="career bg-light-1 section-spacing">
                    <div className="container">
                        <div className="row">
                            <div className="col-xl-12 col-lg-12">
                                <h2 className="f-24">Open Job Positions</h2>

                                <ul className="career-tab-menu list-inline">
                                    <li className={currentSection === "All" ? "active" : ""} 
                                    onClick={()=>this.currentSection('All')} key={0}>All
                                    </li>
                                    {
                                        sectionName && sectionName.length
                                        ?
                                        sectionName.map((d, i) => (
                                            <li className={currentSection === d ? "active" : ""} 
                                         onClick={() => this.currentSection(d)} key={i+1}>{d}</li>
                                        )) : null
                                    }
                                    {/* <li className="active">Analysts</li>
                                    <li>Content Writers</li> */}
                                </ul>
                                {
                                    jobList && jobList.length &&
                                    currentPost.map((dd, index)=>(
                                        <div key={index} className="career-tab-content f-14">
                                            {/* <JobListingBox /> */}
                                            <div className="career-box bg-white text-md-center">
                                                <div className="row">
                                                    <div className="col-xl-8 col-lg-8 col-md-12">
                                                        <h3 className="f-20">{dd.jobName}</h3>
                                                        <ul className="list-inline">
                                                            <li>{dd.jobType}</li>
                                                            <li>{dd.jobLocation}</li>
                                                            <li>{dd.createdAt}</li>
                                                        </ul>
                                                    </div>
                                                    <div className="col-xl-4 col-lg-4 col-md-12 text-right text-md-center md-mt-20">
                                                        <button className="btn btn-border mR20">Apply Now</button>
                                                        <a href=""><i className="onePrep-sprite wishFill-icon"></i></a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    ))
                                }
                                
                                <ul className="pagination text-center mT30">
                                    {
                                        this.state.pageNumber&&this.state.pageNumber.length&&
                                        this.state.pageNumber.map((number)=>(
                                        <li onClick={this.paginate(number)} key={number}>{number}</li>
                                        ))
                                    }
                                    {/* <li>1</li>
                                    <li>2</li>
                                    <li>3</li>
                                    <li><a className="arrow arrow-right"></a></li> */}
                                        <li><a className="arrow arrow-right"></a></li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
                <Subscription />
                <Footer />
            </main>
        );
    }
    
};


const JobListingBox = (joblist) => (
    <div className="career-box bg-white text-md-center">
        <div className="row">
            <div className="col-xl-8 col-lg-8 col-md-12">
                <h3 className="f-20">Customer Support Executive</h3>
                <ul className="list-inline">
                    <li>Full Time</li>
                    <li>Chandigarh</li>
                    <li>Published 4 months ago</li>
                </ul>
            </div>
            <div className="col-xl-4 col-lg-4 col-md-12 text-right text-md-center md-mt-20">
                <button className="btn btn-border mR20">Apply Now</button>
                <a href=""><i className="onePrep-sprite wish-icon"></i></a>
            </div>
        </div>
    </div>
)

export default JobListing;